import { createRouter, createWebHistory } from "vue-router";
import HomePage from "../views/home-page.vue";
import BestDeals from "../views/BestDeals";
import BestDeal from "../views/BestDeal";
import AboutPage from "../views/about-page";
import HolidayPackages from "../views/HolidayPackages";
import HolidayPackage from "../views/HolidayPackage";
import ContactPage from "../views/contact-page";
import ServicePage from "../views/service-page";
//import CartPage from "../views/Cartpage";
import NotFoundpage from "../views/NotFoundpage";

const routes = [
  {
    path: "/",
    name: "HomePage",
    component: HomePage,
  },
  {
    path: "/about",
    name: "AboutPage",
    component: AboutPage,
  },
  {
    path: "/bestdeals",
    name: "BestDeals",
    component: BestDeals,
    props: true,
  },
  {
    path: "/bestdeal/:itemid",
    name: "BestDeal",
    component: BestDeal,
    props: true,
  },
  {
    path: "/holidaypackages",
    name: "HolidayPackages",
    component: HolidayPackages,
    props: true,
  },
  {
    path: "/holidaypackage/:itemid",
    name: "HolidayPackage",
    component: HolidayPackage,
    props: true,
  },
  {
    path: "/contact",
    name: "ContactPage",
    component: ContactPage,
  },

  {
    path: "/services",
    name: "ServicePage",
    component: ServicePage,
  },
  {
    path: "/:pathMatch(.*)*",
    name: "NotFoundpage",
    component: NotFoundpage,
  },
  // {
  //   path: "/cart",
  //   name: "CartPage",
  //   component: CartPage,
  // },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
